.product{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-direction: column;
    max-height: 400px;
    min-width: 100px;
    margin: 10px;
    padding: 20px;
    width: 100%;
    background-color: #fff;
    z-index: 1;
}
.product__info{
    height: 100px;
    margin-bottom: 15px;
}
.product__price{
    margin-top: 5px;
}
.product__rating{
    display: flex;
    color: gold;
}
.product > img{
    max-height:200px;
    width: 100%;
    object-fit: contain;
    margin: 0 0 15px 0;
}
.product > button{
    background-color: #f0c14b;
    border: 1px solid;
    border-color: #a88734,#9c7e31,#846a29;
}